@font-face {
    font-family: 'Bifocals';
    src: url('/public/fonts/bifocals/Bifocals-Regular.eot');
    src: url('/public/fonts/bifocals/Bifocals-Regular.woff') format('woff'),
        url('/public/fonts/bifocals/Bifocals-Regular.otf') format('opentype'),
        url('/public/fonts/bifocals/Bifocals-Regular.svg') format('svg');
}

* {
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

body {
    font-family: 'Bifocals' -apple-system, system-ui, sans-serif;
    font-size: 1.125em;
    font-weight: 400;
    line-height: 150%;
    color: rgba(0, 0, 0, 0.75);
    /* background-color: #11020c; */
    background-color: #000000;
}

.side-text-box {
    position: absolute;
    display: flex;
    align-items: center;
    width: 50%;
    height: 100vh;
}

.side-text {
    font-family: 'Bifocals', sans-serif;
    font-size: 20vh;
    line-height: 20vh;
    font-weight: 400;
    color: #ffffff;
    margin: 0;
    writing-mode: vertical-lr;
    transform: rotate(-180deg);
}

.side-menu {
    position: absolute;
    padding-right: 50px;
    height: 100vh;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    background-color: #000000;
    opacity: 0.75;
}

.title-box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-top: 15px;
    margin-left: 20px;
    margin-bottom: 100px;
}

.title-box h1 {
    font-family: 'Bifocals', sans-serif;
    font-size: 28px;
    line-height: 32px;
    font-weight: 400;
    color: #ffffff;
    margin: 0;
}

.title-box.bottom {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-bottom: 15px;
    margin-left: 20px;
}

.title-box.bottom h1 {
    font-style: italic;
}

.navigation-container {
    position: relative;
    left: 0;
    top: 0;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.navigation-bar {
    height: 2px;
    margin-top: 2.5vh;
    margin-bottom: 2.5vh;
    width: 150px;
    background-color: white;
    opacity: 0.75;
}

.section-box {
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 380px;
}

.paragraph-section {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    height: 100vh;
}

.paragraph-section-right {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.paragraph {
    text-align: justify;
    width: 50vw;
    padding-bottom: 10px;
    padding-right: 10px;
    font-family: 'Bifocals', sans-serif;
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
    color: #ffffff;
    margin: 0;
}

.scrollArea {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: auto;
}

.image-box {
    width: 400px;
    height: 400px;
    position: absolute;
}

.album-image-box {
    width: 50vh;
    height: 50vh;
    position: absolute;
    right: 25%;
}

.para-image-box {
    width: 100%;
    height: 400px;
    position: absolute;
    right: 0;
}

.profile-image-box {
    position: relative;
    width: 90vw;
    height: auto;
}

.image-plane {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0;
}

.para-image-box {
    width: 50vw;
    height: auto;
    position: absolute;
}

.header-white {
    max-width: 200px;
    font-size: 75px;
    line-height: 75px;
    color: white;
}
